.modal {
    position: absolute;
    width: 100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.5);
    top: 0;
    z-index: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
  
    .card {
      background-color: $darkYellow;
      color: $blackColor;
      width: 360px;
      min-height: 100px;
      border-radius: 10px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      position: relative;
      text-align: center;
  
      .card__header {
        // margin: 80px 0;
        height: 20px;
      }
  
      .card__body {
        margin: 10px 0;
      }
    }
  
    .cerrar__modal {
      position: absolute;
      width: 20px;
      fill: black;
      top: 10px;
      right: 10px;
      z-index: 1;
      cursor: pointer;
    }
  }