.oubitaToken {
  font-size: 1rem;
  background-color: $blackColor;
  color: $lightYellow;
  margin-top: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;

  .oubitaContainer {

    width: 80%;
    margin: 8em 0em 6em 0em;

    .oubitaContainer__title {
      font-weight: bold;
      font-size: 3vw;
      margin: 6vw 0em 1em 0;
    }

    .oubitaContainer__copy {
      font-size: 1.25em;
      letter-spacing: 1px;
      line-height: 1.2em;

      .oubitaContainer__rest-disable {
        opacity: 0;
        pointer-events: none;
        margin: 0em auto;

        p {
          margin: 0em 0;
        }

        h5 {
          font-size: 0em;
        }

        transform: translateY(-50px);

        * {
          height: 0px;
          overflow: hidden;
          transition: height 0.2s ease-out;
        }

        transition: all .4s;
      }

      .oubitaContainer__rest-active {
        opacity: 1;
        pointer-events: stroke;
        margin: 1.4em auto;

        p {
          margin: 1.3em 0;
        }

        h5 {
          font-size: 1.3em;
        }

        transform: translateY(0px);

        * {
          height: auto;
          overflow: hidden;
        }

        transition: all 1s;
      }
    }

    .--oubitaContainer__buton {
      display: block;
      margin: 3em 0em 0em auto;
      font-size: 1.8em;
      padding: 0.3em 1em;
      display: flex;
      justify-content: center;
      align-items: center;

      .oubita__arrow {
        width: 0.7em;
        fill: #f6c22d;
        margin: 0em 0em 0em 0.7em;
      }
    }
  }

  @media (max-width: 1030px) {
    .oubitaContainer {
  
      width: 80%;
      margin: 8em 0em 6em 0em;
  
      .oubitaContainer__title {
        font-weight: bold;
        font-size: 3vw;
        margin: 6vw 0em 1em 0;
      }
  
      .oubitaContainer__copy {
        font-size: 1.25em;
        letter-spacing: 1px;
        line-height: 1.2em;
  
        .oubitaContainer__rest-disable {
          opacity: 0;
          pointer-events: none;
          margin: 0em auto;
  
          p {
            margin: 0em 0;
          }
  
          h5 {
            font-size: 0em;
          }
  
          transform: translateY(-50px);
  
          * {
            height: 0px;
            overflow: hidden;
            transition: height 0.2s ease-out;
          }
  
          transition: all .4s;
        }
  
        .oubitaContainer__rest-active {
          opacity: 1;
          pointer-events: stroke;
          margin: 1.4em auto;
  
          p {
            margin: 1.3em 0;
          }
  
          h5 {
            font-size: 1.3em;
          }
  
          transform: translateY(0px);
  
          * {
            height: auto;
            overflow: hidden;
          }
  
          transition: all 1s;
        }
      }
  
      .--oubitaContainer__buton {
        display: block;
        margin: 3em 0em 0em auto;
        font-size: 1.8em;
        padding: 0.3em 1em;
        display: flex;
        justify-content: center;
        align-items: center;
  
        .oubita__arrow {
          width: 0.7em;
          fill: #f6c22d;
          margin: 0em 0em 0em 0.7em;
        }
      }
    }
  
    .--arrow-down {
      transform: rotate(180deg);
    }
  
    @media (max-width: 1030px) {
      .oubitaContainer {
        .oubitaContainer__title {
          font-weight: bold;
          font-size: 2.8em;
          margin: 6vw 0em 1em 0;
        }
      }
    }
  
    @media (max-width: 720px) {
      .oubitaContainer {
        .oubitaContainer__title {
          font-weight: bold;
          font-size: 2.5em;
          margin: 0em 0em 1em 0;
        }
  
        .--oubitaContainer__buton {
          font-size: 1.5em;
          display: block;
          margin: 2em auto 0em auto;
        }
      }
    }
  
    @media (max-width: 520px) {
      .oubitaContainer {
  
        width: 90%;
        margin: 3em 0em;
  
        .oubitaContainer__title {
          font-size: 2em;
          margin: 0em 0em .4em 0;
        }
  
        .oubitaContainer__copy {
          font-size: .9em;
        }
  
        .--oubitaContainer__buton {
          font-size: 1.2em;
          display: block;
          margin: 2em auto 0em auto;
        }
      }
    }
  } 
}