.layout {
    display: grid;
    min-height: 100vh;
    color: $blackColor;
    grid-template-areas:
      'header'
      'contenido'
      'footer';
  
    grid-template-rows: 60px 10fr auto;
  
    .header {
      grid-area: header;
      min-width: 360px;
  
    }
  
    .content {
      grid-area: contenido;
      min-width: 360px;
  
    }
  
    .footer {
      grid-area: footer;
    }
  
    background-color: $lightYellow;
    position: relative;
    overflow: hidden;
    z-index: 1;
  }