.profile {
    font-size: 1rem;
    margin: 6vw auto;
  
    .profile__header {
      display: flex;
      flex-direction: column;
      align-items: center;
  
      .profile__ContainerImg {
        background-color: $darkYellow;
        border: 1px solid $blackColor;
        border-radius: 100%;
        width: 6em;
        height: 6em;
        position: relative;
  
        .img {
          display: block;
          width: 40%;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }
  
      .profile__Wallet {
        background-color: $blackColor;
        color: $lightYellow;
        border-radius: 100px;
        padding: .5em 1em;
        margin: 1.2em 1em;
      }
  
      .profile__form {
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 60%;
  
        .container__input {
          width: 100%;
          position: relative;
  
          .iconPen {
            width: 30px;
            position: absolute;
            z-index: 1;
            top: 50%;
            right: 10px;
            transform: translate(0, -50%);
            fill: $lightYellow;
            cursor: pointer;
          }
  
          .profile__input {
            font-size: 1em;
            font-weight: bold;
            color: $lightYellow;
            width: 100%;
            padding: .8em 1em;
            margin: 1em 0em;
            // background-color: $blackColor;
            background-color: rgb(51 51 51 / 70%);
            ;
            border-radius: 100px;
            border: 1px solid $lightYellow;
            box-shadow: 0em 0em 0.3em $blackColor;
  
            &::placeholder {
              color: $lightYellow;
            }
  
            &:focus {
              outline: 1px solid $lightYellow;
            }
          }
  
        }
  
        .--form__botton {
          padding: 1em 3em;
          font-size: .7em;
          align-self: flex-start;
          margin: 0 6px;
        }
      }
    }
  
    .profile__body {
      margin: 1.5em auto .7em auto;
      display: flex;
      justify-content: center;
  
      .profile__body_titile {
        font-size: 1.3em;
      }
    }
  
    @media (max-width: 1034px) {
      font-size: 1rem;
      margin: 4em auto;
  
    }
  
    @media (max-width: 720px) {
      font-size: .7rem;
      width: 90%;
  
      .profile__header {
        .profile__form {
          width: 310px;
  
          .container__input {
            .iconPen {
              width: 20px;
            }
          }
  
        }
      }
    }

     // NFT CONTAINER 

    .profile_button_container {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      outline: none;
      border: none;  
      
      .profile_buttons{
          margin-left: 50px;
      }
  }
  
  .profile_nft_container{
      
      
      .profile_nft{
          display: grid;
          grid-template-columns: repeat(5, 1fr);
          text-align: center;


          .profile_nft_container{
              font-weight: bold;
              font-size: 0.8em;
              margin: 10px;
              font-family: "Roboto", sans-serif;
              

              .profile_nft_cointainer_id_div{
                  height: 25px;
      

                  .profile_nft_id{
                      margin-bottom: 5px;
                  }
              }


              .profile_nft_container_img_div{
                  height: 190px;
                  width: 220px;


                  .profile_nft_container_imgSize{
                      height:100%;
                      width: 100%;
                  }

  

                  .profile_nft_container_button {
                      height:190px;
                      width: 210px;
                      border: 0px;
                      background-color: #f6c22d;
                      cursor: pointer;
                      
                  }
              }

              .profile_nft_container_name_div{
                  height: 25px;
                  text-align: center;
                  margin-top: 5px;
                  margin-bottom: 5px;          
              }
          }
          
      }
  }

  //POPOUP

  .profile_popup{
      position:absolute;
      top:0;
      left:0;
      width:100%;
      height: 100vh;
      display:flex;
      justify-content: center;
      align-items: center;
      z-index: 100;
      
      .profile_popup_innner {
          position:absolute;
          padding: 32px;
          height: 550px;
          width: 550px;
          max-width: 1000px;
          background-color: #f6c22d;
          border: solid 3px;
          border-color: #333333;
              
          .profile_popup_nft{
            
              height: 450px;
              width: 450px;
              margin-top: 10px;
              border: solid 2px;
                  
          }
          
      }

      .profile_popup_close_btn{
          position:absolute;
          top: 7px;
          right: 260px;
          height: 30px;
          width: 30px;
          background-color: #333333;
          color: #f6c22d;
          border: #f6c22d;
          font-weight: bold;
          border-radius: 5em;
          box-shadow: 0px 0px 1em -5px #333333;
          cursor: pointer;
      }
}
  }