$timeAnimation: .2s;

.menuComponetContenedorBenefits {
  font-size: 1em;
  font-weight: bold;

  .menuComponet__header {
    color: $lightYellow;
    // color: red;
    background-color: $blackColor;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 100px;
    padding: 0.2em 0em 0.2em 1em;
    font-size: 2.1em;
    border: $lightYellow solid 2px;
    margin: 0.3em 0em;
    height: 2em;
    cursor: pointer;

    .menuComponet__title {
      width: 100%;
      text-align: center;
      text-transform: uppercase;
    }

    .menuComponet__arrow {
      width: .8em;
      fill: #f6c22d;
      margin-right: 1em;
    }
  }

  .menuComponet__body {
    color: $blackColor;
    width: 95%;
    font-size: 1.3em;
    line-height: 1.2em;
    text-align: justify;

  }

  .--disable {
    opacity: 0;
  //  pointer-events: none;
    margin: 0em auto;
    transform: translateY(-50px);

    .menuComponet__body__text {
      height: 0px;
      overflow: hidden;
      transition: height $timeAnimation ease-out;
    }

    transition: all $timeAnimation;
  }

  .--active {
    opacity: 1;
  //  pointer-events: stroke;
    margin: 1.4em auto;
    transform: translateY(0px);

    .menuComponet__body__text {
      height: auto;
      overflow: hidden;
    }

    transition: all $timeAnimation;
  }

  @media (max-width: 920px) {
    .menuComponet__header {
      height: 2.5em;
    }

    .menuComponet__body {
      font-size: 1.5em;

    }
  }
}

