.faqs {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: .5em;
    position: relative;
  
    .faqs__title {
      font-weight: bold;
      font-size: 3em;
      margin: 1em 0;
    }
  
    .fasq__container {
      width: 60%;
      font-size: .8em;
    }
  
    @media (max-width: 1034px) {
  
      .fasq__container {
        width: 75%;
        font-size: .7em;
      }
  
      margin: auto;
    }
  
    @media (max-width: 917px) {
      .fasq__container {
        width: 80%;
        font-size: .7em;
  
      }
    }
  
    @media (max-width: 720px) {
      .fasq__container {
        width: 90%;
        font-size: .7em;
  
      }
    }
  
    @media (max-width: 520px) {
      .fasq__container {
        width: 90%;
        font-size: .7em;
      }
    }
  
    @media (max-width: 360px) {
      .fasq__container {
        width: 96%;
      }
    }
  
  }