$timeAnimation: 1s;

.menu {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-end;
  font-size: 1.2vw;
  height: 5.5vw;
  padding: 0px 30px;
  position: relative;
  z-index: 100;

  .logoMenu {
    position: absolute;
    width: 10vw;
    max-width: 150px;
    top: 20px;
    .logo{
      width: 100%;
    }
  }

  .navegation {
    flex-grow: 1;
    text-align: center;

    .navegation--icon {
      margin-bottom: .375em;

      .icon {
        fill: $blackColor;
        width: 1.1em;
        height: 1.1em;
        margin: 0em 0.3em;

        &:hover {
          animation: iconAnimate .7s;
          animation-fill-mode: forwards;
        }

        @keyframes iconAnimate {
          0% {
            transform: scale(1)
          }

          25% {
            transform: scale(1.4)
          }

          50% {
            transform: scale(1)
          }

          75% {
            transform: scale(1.3)
          }

          100% {
            transform: scale(1.2)
          }
        }
      }
    }

    .navegation--list {
      font-size: 1em;

      .link {
        color: $blackColor;
        text-decoration: none;
        font-weight: bold;
        font-size: .8em;
        margin: .0625em .625em;
        cursor: pointer;
        display: inline-block;
      }
    }
  }

  .--botonMenu {
    position: absolute;
    right: 3vw;
    font-size: 0.8em;
    padding: .625em 2.0625em;
    transition: $timeAnimation;
  }

  @media (max-width: 1366px) {
    display: flex;
    align-items: flex-end;
    font-size: 1rem;
    height: 60px;
    padding: 0px 30px;

  }

  @media (max-width: 1034px) {
    // font-size: .9rem;
    font-size: 1.5vw;
  }

  @media (max-width: 917px) {
    // font-size: .76rem;
    padding: 0px 12px;
    margin-left: 14px;
  }

  @media (max-width: 720px) {
    font-size: 1.6rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0;
    margin: 0px !important;

    .navegation {
      background-color: $darkYellow;
      width: 100%;
      position: relative;
      z-index: 5;
      padding-top: 14px;

      .navegation--list {
        display: flex;
        flex-direction: column;

        .link {
          margin: .4375rem 0rem;
        }
      }

      transition: $timeAnimation;
    }
  }

  //menu movil o portatil---------------------------------------------


  @media (max-width: 720px) {
    .menu-movil {
      position: relative;
      width: 100%;
      height: 60px;
      margin: 0px !important;

    }

    .hamburguesa {
      position: absolute;
      z-index: 6;
      right: 0.7em;
      top: 4px;
      display: inline-block;
      cursor: pointer;
    }

    .bar1,
    .bar2,
    .bar3 {
      width: 35px;
      height: 5px;
      background-color: #333;
      margin: 6px 0;
      transition: 0.4s;
    }

    .change1 {
      transform: rotate(-45deg) translate(-9px, 6px);
    }

    .change2 {
      opacity: 0;
    }

    .change3 {
      transform: rotate(45deg) translate(-8px, -8px);
    }

    .--desaparecer {
      opacity: .0;
      position: absolute;
      transform: translateY(-100px);
      pointer-events: none;
    }

    .navegation {
      position: absolute;
      left: 0px;
      padding-bottom: 2.7em;
    }

    .--botonMenu {
      font-size: .54em;
      position: absolute;
      right: 50%;
      transform: translateX(50%) ;
      top: 22em;
      z-index: 6;
    }
  .logoMenu {
    position: absolute;
    width: 10vw;
    top: 10px;
    left: 10px;
    .logo{
      width: 100%;
    }
  }
  
  }

  @media (max-width: 520px) {
    font-size: 0.8em;

    .--botonMenu {
      font-size: .50em;
      position: absolute;
      top: 26em;
      z-index: 6;
    }
  }


  @media (max-width: 360px) {
    font-size: 0.7em;

    .--botonMenu {
      top: 26.5em;
      padding: 10px 34px;
    }

    .menu-movil {
      position: relative;
      // background-color: rgb(9, 255, 0);
      width: 100%;
      // height: 6.25rem;
    }

    .hamburguesa {
      position: absolute;
      // right: 51px;
      top: 4px;
      display: inline-block;
      cursor: pointer;
    }

    .bar1,
    .bar2,
    .bar3 {
      width: 35px;
      height: 5px;
      background-color: #333;
      margin: 6px 0;
      transition: 0.4s;
    }

    .change1 {
      transform: rotate(-45deg) translate(-9px, 6px);
    }

    .change2 {
      opacity: 0;
    }

    .change3 {
      transform: rotate(45deg) translate(-8px, -8px);
    }


  }

  .menu_popup{
    position:fixed;
    top:0;
    left:0;
    width:100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.2);
    display:flex;
    justify-content: center;
    align-items: center;
    z-index: 100;

    
    .menu_popup_innner {
        position:relative;
        padding: 32px;
        height: 300px;
        width: 1000px;
        height: 430px;
        max-width: 1200px;
        background-color: #333333;
        border-radius: 30px;
        color: #f6c22d;
        
            
      
      .menu_div{
      position: relative;
      top: 45%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      height: 100%;
      display: grid;
      grid-template-columns: repeat(1, 1fr);
      text-align: center;
      

        .menu_div_img{
        
        height: 250px;
        width: 100%;

        .menu_img{
          position: absolute;
          height: 400px;
          width: 380px;
          top: -15%;
          left: 29%;
        }
        }
    
        .menu-div-subtitle1 {
        height: 250px;
        width: 280px;
  
        
          .menu__titulo1_1{
            font-size: 20px;
            font-weight: bold;
              
          }

          .menu__titulo3{
            font-size: 20px;
            font-weight: bold;     
          }
  
          .menu__titulo4{
            font-size: 20px;
            font-weight: bold;
          }
    
        }

        .menu_div_subtitle2{
        
          .menu__titulo2_1{
            font-size: 18px;
            font-weight: bold;
          }
        }
      }
        
      .menu_popup_nft{
            height: 380px;
            width: 330px;
            margin-top: 35px;
            margin-right: 20px;
            border: 2px;
            border-color: #333333;
            
      }
      
      .menu_popup_close_btn{
          position:absolute;
          top:25px;
          right: 135px;
          height: 30px;
          width: 30px;
          background-color: #333333;
          color: #f6c22d;
          border: #f6c22d;
          font-weight: bold;
          border-radius: 5em;
          box-shadow: 0px 0px 1em -5px #333333;
          cursor: pointer;
      }
    }
  }
}