.mint {
  margin-bottom: 40px;
  margin-top: 6em;
  font-size: 1vw;
  position: relative;
  z-index: 10;
  .mint__container {
    width: 70%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    margin-bottom: 80px;

    .mint__content {
      width: 40em;
      align-self: flex-end;

      .titulo__mint {
        font-size: 5em;
        font-weight: bold;
        line-height: 1.1em;
      }

      .sub__titulo {
        font-size: 2em;
        font-weight: bold;
        margin-bottom: 33px;
      }

      .sub__titulo1 {
        font-size: 2.69em;
        font-weight: bold;
        // margin-bottom: 33px;
      }

      .parrafo__mint {
        font-size: 1.2em;
        margin-bottom: 18px;
      }

      // .--boton__mint {
      //   padding: 4px 66px;
      //   font-size: 1.1em;
      // }
      // .link__boton {
      //   font-size: 1.5em;
      //   font-weight: bold;
      //   text-decoration: none;
      //   color: #f6c22d;
      // }
      .botones_mint {
        display: flex;
        align-items: flex-start;

        .--boton__mint {
          padding: .5em 3em;
          font-size: 1.3em;
        }

        .--botonNFT {
          margin-right: 1.5em;
        }

        // .--botonETH {}
      }

    }

    .mint__img {

      .img {
        width: 17em;
      }
    }
  }

  @media (max-width: 1366px) {
    font-size: .75rem;
    margin-top: 3.5em;

  }

  @media (max-width: 1034px) {
    font-size: .7rem;
  }

  @media (max-width: 917px) {
    font-size: .6rem;
    .mint__container {
      width: 80%;
    }
  }

  @media (max-width: 720px) {
    margin-top: 30px;

    .mint__container {
      width: 100%;

      display: flex;
      flex-direction: column;
      align-items: center;
      font-size: .6rem;

      .mint__content {
        width: 90%;
        align-self: center;
        margin-bottom: 30px;

        // .--boton__mint {
        //   display: block;
        //   margin: auto;
        // }

      }
    }
  }


  //INICIO DEL FOOTER DE MINT------------------------------------------------------------
  .mint__footer {
    font-size: .7rem;
    position: relative;
    z-index: -2;
    display: flex;
    justify-content: space-between;
    background-color: $blackColor;
    color: $lightYellow;

    .footer__content {
      display: flex;
      align-items: center;
      font-weight: bold;
      margin: 0 0 0 10vw;

      .footer__title {
        font-size: 2.5em;
        width: 12em;
      }

      .--medio-border {
        background-color: $lightYellow ;
        width: 10px;
        height: 70%;
        margin: 0 30px 0 30px;
      }

      .footer__copy {
        font-size: 2.4em;
        line-height: 1.5em;
      }
    }

    .footer__logo {
      position: relative;
      // background-color: $lightYellow;
      // border-radius: 50% 0 0 50%;
      width: 17em;
      height: 14em;
      // margin-top: 1.4em;
      // margin-bottom: 1.4em;

      .image {
        position: absolute;
        top: 50%;
        left: -31%;
        transform: translate(-49%, -49%);
        width: 22.5em;
      }
    }
  }

  @media (max-width: 1366px) {
    .mint__footer {
      .footer__content {
        margin: 0 0 0 8vw;

        .footer__title {
          font-size: 2.2vw;
          width: 13em;
        }

        .--medio-border {
          background-color: $lightYellow ;
          width: 7px;
          height: 70%;
          margin: 0 30px 0 0px;
        }

        .footer__copy {
          font-size: 2vw;
          line-height: 1.5em;
        }
      }

      .footer__logo {
        width: 15em;
        height: 13em;

        .image {
          left: 9%;
          width: 20.3em;
        }
      }
    }
  }

  @media (max-width: 720px) {
    .mint__footer {
      // flex-direction: column;
      justify-content: space-around;
      .footer__content {
        margin: 10px 0px 0px 0px;
        align-items: center;
        justify-content: center;
        flex-direction: column;


        .footer__title {
          font-size: 3.2vw;
          width: 12em;
        }

        .footer__copy {
          font-size: 2.2vw;
          margin:3px 0 0 0;
        }

        .--medio-border {
          height: 2px;
          width: 100%;
          margin: 0 2vw 0 2vw;
        }
      }

      .footer__logo {
        align-self: center;
        // order: 1;
        border-radius: 49%;

        // width: 27em;
        .image {
          left: 50%;
          width: 22em;
        }

      }
    }
  }

  @media (max-width: 550px) {
    .mint__footer {
      .footer__content {
        margin: 10px 0px 0px 0px;
        align-items: center;
        justify-content: center;

        .footer__title {
          font-size: 3.5vw;
          width: 12em;
        }

        .footer__copy {
          font-size: 2.7vw;
        }

        // .--medio-border {
        //   min-height: 17vw;
        //   width: .7vw;
        //   margin: 0 2vw 0 2vw;
        // }
      }

      .footer__logo {
        font-size: 2vw;
        align-self: center;
        border-radius: 49%;

        .image {

          left: 50%;
          width: 20em;
          // top: 10em;
      
        }

      }
    }
    
  }

  // POPUP
  
  .mint_popup{
    position:fixed;
    top:0;
    left:0;
    width:100%;
    height: 100vh;
    background-color: rgba(0, 0, 0, 0.2);
    display:flex;
    justify-content: center;
    align-items: center;
    z-index: 100;

    
    .mint_popup_innner {
        position:relative;
        padding: 32px;
        height: 300px;
        width: 1000px;
        height: 430px;
        max-width: 1200px;
        background-color: #333333;
        border-radius: 30px;
        color: #f6c22d;
        
            
      
      .mint_div{
      position: relative;
      top: 45%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      height: 100%;
      display: grid;
      grid-template-columns: repeat(1, 1fr);
      text-align: center;
      

        .mint_div_img{
        
        height: 250px;
        width: 100%;

        .mint_img{
          position: absolute;
          height: 400px;
          width: 380px;
          top: -15%;
          left: 29%;
        }
        }
    
        .mint-div-subtitle1 {
        height: 250px;
        width: 280px;
  
        
        .mint__titulo1_1{
          font-size: 20px;
          font-weight: bold;
              
        }

        .mint__titulo3{
          font-size: 20px;
          font-weight: bold;     
        }
  
        .mint__titulo4{
          font-size: 20px;
          font-weight: bold;
        }
    
        }

        .mint_div_subtitle2{
        
        .mint__titulo2_1{
          font-size: 18px;
          font-weight: bold;
        
        }
        }
      }
        
      .mint_popup_nft{
            height: 380px;
            width: 330px;
            margin-top: 35px;
            margin-right: 20px;
            border: 2px;
            border-color: #333333;
            
      }
      
      .mint_popup_close_btn{
          position:absolute;
          top:25px;
          right: 135px;
          height: 30px;
          width: 30px;
          background-color: #333333;
          color: #f6c22d;
          border: #f6c22d;
          font-weight: bold;
          border-radius: 5em;
          box-shadow: 0px 0px 1em -5px #333333;
          cursor: pointer;
      }
    }
  }
  //FINAL DEL FOOTER DE MINT------------------------------------------------------------
}
