.footerComponent {
    font-size: 1rem;
    background-color: $blackColor;
    margin-top: 3em;
  
    .footer__container {
      width: 80%;
      margin: auto;
      display: flex;
      justify-content: space-between;
      line-height: 1.5em;
  
      .footer__img {
        align-self: center;
        width: 12vw;
  
        .img {
          width: 70%;
        }
      }
  
      .footer__content {
        margin: 2em 0em;
  
        .footer__content_title {
          color: $lightYellow;
          margin: .7em 0em;
          font-size: 2vw;
        }
  
        p {
          color: $whiteColor;
          font-size: 1vw;
  
        }
      }
  
      .--content3 {
        width: 24vw;
      }
  
      .search {
        display: flex;
        height: 2vw;
        border: 3px solid $lightYellow;
        border-radius: 100px;
        overflow: hidden;
        margin: 15px 0px;
        position: relative;
  
        .inputSearch {
          padding: 0;
          width: 80%;
          height: 100%;
          border: none;
          background-color: $blackColor ;
          color: $lightYellow;
          font-size: 1em;
        }
  
        .inputSearch:focus {
          outline: none;
        }
  
        .buttomIcon {
          border: none;
          border-radius: 100px;
          position: absolute;
          right: 0px;
          background-color: $lightYellow;
          fill: $blackColor;
          width: 20%;
          height: 100%;
        }
      }
  
      .footer__socialIcon {
        width: 25%;
        margin: auto -25px;
  
        .icon {
          fill: $lightYellow;
          width: 2.5vw;
          margin: 0em 2vw;
        }
      }
    }
  
    @media (max-width: 1034px) {
      font-size: 0.7rem;
  
      .footer__container {
        width: 90%;
  
        .footer__content {
          .footer__content_title {
            font-size: 1em;
          }
  
          p {
            font-size: 1em;
          }
        }
  
        .search {
          height: 2.5vw;
        }
      }
    }
  
    @media (max-width: 720px) {
      .footer__container {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
  
        .footer__img {
          align-self: center;
          width: 100%;
          margin-top: 10vw;
  
          .img {
            width: 20vw;
            display: block;
            margin: auto;
          }
        }
  
        .footer__content {
          margin: 2em 0em;
          text-align: center;
          font-size: 3vw;
          line-height: 4vw;
  
          .footer__content_title {
            font-size: 4vw;
          }
  
          .search {
            height: 6vw;
          }
        }
  
        .--content1 {
          width: 50%;
          text-align: center;
  
          .footer__content_title {
            margin-left: -6vw;
          }
        }
  
        .--content2 {
          width: 50%;
          text-align: center;
  
          .footer__content_title {
            margin-left: -6vw;
          }
        }
  
        .--content3 {
          width: 70%;
          text-align: left;
  
          .footer__content_title {
            text-align: center;
            margin-top: -4vw;
  
          }
  
          .PAux {
            display: none;
          }
        }
  
        .footer__socialIcon {
          width: 100%;
          display: flex;
          justify-content: center;
          margin: -1em 0 2em 0;
  
          .icon {
            fill: $lightYellow;
            width: 8vw;
            margin: 0em 2vw;
          }
        }
      }
    }
  }