.benefits {
  font-size: .9vw;
  width: 75%;
  margin: auto;
  margin-left: auto;
  margin-top: 3em;
  display: flex;
  justify-content: space-between;
  font-weight: bold;
  position: relative;
  .benefits__content {

    width: 100%;
    margin-right: 4em;

    .benefits__title {
      font-size: 3.5em;
      font-weight: bold;
      width: 65%;
      margin: .5em 0;
      line-height: 1.2em;
    }

    .benefits__menu {
      font-size: .85em;
      // margin-top: 7em;
      position: relative;
      z-index: 2;
    }

  }

  .benefits__galeria {
    .benefits__img {
      width: 35em;
    }
  }

  @media (max-width: 720px) {
    flex-direction: column;
    width: 80%;
    font-size: .7rem;
    margin: auto;
    margin-left: auto;
    margin-top: 3em;
    display: flex;
    justify-content: space-between;
    font-weight: bold;
    position: relative;
  
    .benefits__content {
  
      width: 100%;
      margin-right: 4em;
  
      .benefits__title {
        font-size: 3.5em;
        font-weight: bold;
        width: 65%;
        margin: .5em 0;
        line-height: 1.2em;
      }
  
      .benefits__menu {
        font-size: .85em;
        // margin-top: 7em;
  
      }
  
    }
  
    .benefits__galeria {
      .benefits__img {
        width: 35em;
      }
    }
  
    @media (max-width: 720px) {
      flex-direction: column;
      width: 80%;
      font-size: .7rem;
      margin: auto;
  
      .benefits__content {
        .benefits__title {
          font-size: 3em;
          margin-top: 2em;
        }
      }
  
      .benefits__galeria {
  
        .benefits__img {
          font-size: 1.2vw;
          margin-top: 17vw;
          width: 100%;
        }
      }
    }
  
    @media (max-width: 520px) {
      flex-direction: column;
      width: 95%;
      font-size: .7rem;
      margin: auto;
  
    }
  }
}